import React, { Component } from 'react'
import ClientService from '../../../services/ClientService'
import AsyncSelect from 'react-select/lib/Async';



export default class LoanList extends Component {
    state = {
        clients: [],
        getClients: false,
    }
    service = new ClientService();

    handleChange = (event) => {
      const { name, value } = event.target;
      if (value === "" || null || undefined ) {
        this.setState({ getClients: false, clients: [] })
      } else {
        this.setState({ [name]: value, getClients: true });
      }
    }

    handleInputChange = (inputValue) => {  

      this.loadOptions(inputValue)
    };

    loadOptions = (inputValue) => {
      return this.fetchClients(inputValue)
    };

    fetchClients = async (inputValue) => {
      if (inputValue && (inputValue !== '')) {
      return this.service.getClients(this.props.userLocation, inputValue)
          .then( response => { 
            let clients = response.map( e => { return (e.firstName)})
            this.setState({clients})
            return clients
          })
          .catch(err => {
              this.setState({
                  clients: [],
              })
          })
        }
    }

    onKeyPress(event) {
      if (event.which === 13 /* Enter */) {
          this.fetchClients();
      }
  }

  render() {

    const { clients } = this.state
    
    return (
      <div className="content">
        <AsyncSelect
          cacheOptions
          onInputChange={this.handleInputChange}
          options={clients}
          
          placeholder=""
        />
        <div class="input-group mb-3">
          <div class="input-group-prepend">
            <span class="input-group-text" id="basic-addon1">Buscar cliente</span>
          </div>
          <input type="text" name="fullName" onKeyDown={(e) => this.onKeyPress(e)} onChange={ (e) => this.handleChange(e)} class="form-control" aria-label="Username" aria-describedby="basic-addon1"/>
        </div>
        {/* { (clients.length > 0) ? <ClientTable data={clients}/> : ""} */}
        
      </div>
    )
  }
}


